import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";
import Repairs from "../views/Repairs.vue";
import BuySell from "../views/BuySell";
import Business from "../views/Business";
import Faq from "../views/Faq";
import Rate from "../views/Rate";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: { transition: "fade" }
  },
  {
    path: "/buyandsell",
    name: "buyAndSell",
    component: BuySell,
    meta: { transition: "fade" }
  },
  {
    path: "/rsu",
    name: "Repairs, service, upgrades",
    component: Repairs,
    meta: { transition: "fade" }
  },
  {
    path: "/business",
    name: "Business IT Solutions",
    component: Business,
    meta: { transition: "fade" }
  },
  {
    path: "/faq",
    name: "FAQ",
    component: Faq,
    meta: { transition: "fade" }
  },
  {
    path: "/rate",
    name: "Rate",
    component: Rate,
    meta: { transition: "fade" }
  }
];

const router = createRouter({
  mode: "history",
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to) {
    if (to.hash) {
      return document
        .querySelector(to.hash)
        .scrollIntoView({ behavior: "smooth" });
    } else {
      return document
        .querySelector("#app")
        .scrollIntoView({ behavior: "smooth" });
    }
  }
});

export default router;
