<template>
  <div class="rate">
    <div id="loading">
      <span
        class="iconify"
        data-icon="ant-design:loading-3-quarters-outlined"
        data-inline="false"
      ></span>
    </div>
    <div class="powr-comments" id="515c1b65_1612879229"></div>
  </div>
</template>

<script>
export default {
  name: "Rate",
  created() {
    setTimeout(() => {
      document.querySelector("#loading").style.opacity = "0";
      setTimeout(() => {
        document.querySelector("#loading").style.display = "none";
      }, 500);
    }, 3000);
  },
  mounted() {
    let externalScript = document.createElement("script");
    externalScript.setAttribute(
      "src",
      "https://www.powr.io/powr.js?platform=html"
    );
    document.head.appendChild(externalScript);
  }
};
</script>

<style lang="scss" scoped>
.rate {
  width: 100%;
  min-height: calc(100vh - 50px);
  padding: 2rem 0;
  position: relative;
}
#loading {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  font-size: 3rem;
  color: #222;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.2s linear;
}
.iconify {
  animation: loading 2s infinite;
}
@keyframes loading {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>
